import React from "react";
import { useSelector } from "react-redux";
import { useNavigate, useParams } from "react-router-dom";
import SubHeading from "../Header/SubHeading";
import classes from "./MenuItemDetail.module.css";
import { MenuImages } from "../../constants/images";

const MenuItemDetail = () => {
  const juices = useSelector((state) => state.menu.juices);
  const dishes = useSelector((state) => state.menu.dishes);
  const data = [...juices, ...dishes];
  const params = useParams();
  const detail = data.find((menu) => menu.id === params.orderitemid);
  const navigate = useNavigate();

  const toHomeHandler = () => {
    navigate(`/home`, { replace: true });
  };

  return (
    <section className="app-bg flex-center section-padding ">
      <div className={classes.menuitemdetail}>
        <figure className={classes[`menuitemdetail-img`]}>
          <img src={MenuImages[detail.id]} alt={detail.desc} />
        </figure>
        <div>
          <div>
            <h1 className="headtext-cormorant">{detail.title}</h1>
            <SubHeading
              title={
                detail.id.startsWith(`gd`)
                  ? "Hope You're Ready For Some Authentic Italian Cuisine"
                  : "The Best Drinks To Quench Your Thirst"
              }
            />
            <p className={`p-opensans ${classes[`menuitemdetail-p`]}`}>
              {detail.desc}
            </p>
          </div>
          <br />
          <div></div>
          <button
            onClick={toHomeHandler}
            type="button"
            className="custom-button"
          >
            Go Back
          </button>
        </div>
      </div>
    </section>
  );
};

export default MenuItemDetail;
